<template>
  <div class="infinite-list-wrapper" id="goodsListComponent" ref="goodsListComponent" style="overflow:auto">


    

    <el-row
      class="list"
      v-infinite-scroll="getNextPage"
      infinite-scroll-disabled="disableLoding"
      infinite-scroll-delay="1000"
    >
      <el-col
        :span="11"
        v-for="(item, index) in goodsList"
        :key="index"
        :offset="index%2 > 0 ? 2 : 0"
        class="goodsListCard"
      >
        <el-card :body-style="{ padding: '0px' }" @click.native="goToGoodsDetails(item.id)">
          <!-- <img :src="item.mainPic" class="image" /> -->
          <el-image :src="item.mainPic" style="width: 100%; height: 100%" lazy>
            <div slot="placeholder" class="image-slot">
              <i class="el-icon-picture-outline" style="width: 100%; height: 100%"></i>
            </div>
          </el-image>

          <div style="padding: 14px;">
            <span class="goodsTitle"> {{item.dtitle}}</span>
            <div class="bottom clearfix">
              <div class="tag">
                <el-row>
                  <!-- 优惠券标签 -->
                  <el-col :span="8">
                    <el-tag type="danger" size="mini" effect="dark">
                      <span style="color:#ffffff">{{item.couponPrice}} 元卷</span>
                    </el-tag>
                  </el-col>

                  <!-- 天猫店标签 -->
                  <el-col :span="8" :offset="8" v-if="item.shopType==1">
                    <el-tag type="danger" size="mini" effect="plain">
                      <span>天猫店</span>
                    </el-tag>
                  </el-col>
                </el-row>
              </div>

              <div class="nowPrice" style="margin-top: 10px;">
                <el-row>
                  <el-col :span="16">
                    <h6 style="color:red;text-align:left;">
                      券后 ￥
                      <span style="font-size:18px">{{item.actualPrice}}</span>
                    </h6>
                  </el-col>

                  <el-col :span="8" v-if="item.brand==1">
                    <el-tag type="success" size="mini" effect="plain">
                      <span>精品店</span>
                    </el-tag>
                  </el-col>

                  <el-col :span="8" v-if="item.brand==0">
                    <el-tag type="success" size="mini" effect="plain">
                      <span>优选店</span>
                    </el-tag>
                  </el-col>
                </el-row>
              </div>

              <div class="otherInfo">
                <el-row>
                  <el-col :span="8">
                    <h6>原价￥{{item.originalPrice}}</h6>
                  </el-col>
                  <el-col :span="12" :offset="4">
                    <h6 v-if="item.monthSales>1000">已售:{{ (item.monthSales/10000).toFixed(2) }} 万</h6>
                    <h6 v-if="item.monthSales<1000"> 已售:{{ item.monthSales }} 件</h6>
                  </el-col>
                </el-row>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name:"GoodsList",
  props: ["goodsList"],
  data() {
    return {
      disableLoding: false,
      duration: -1,
      canSendHttp: true
    };
  },

  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    //  document.querySelector('#goodsListComponent').addEventListener('scroll', this.handleScroll);
  },
  methods: {
    getNextPage() {
      // 获取下一页
      // 当disableLoding为false时候 代表不禁止加载 可以滚动

      // 需要由滚动条来通知
      // this.disableLoding = false
      // Toast({
      //   message: "正在加载",
      //   // iconClass: 'el-icon-info',
      //   position: "bottom",
      //   duration: 1000
      // });
      this.disableLoding = false;
      this.$emit("nextPage");
     // console.log("获取成功" + this.disableLoding);
      //禁止获取下一页
      this.disableLoding = true;
      this.canSendHttp = true;

      // Toast({
      //   message: "加载成功",
      //   // iconClass: 'el-icon-success',
      //   position: "bottom",
      //   duration: 1000
      // });
    },

    // 计算滚动条
    handleScroll() {

      var scrollTop = document.documentElement.scrollTop;
      var scrollHeight = document.documentElement.scrollHeight;
      var windowHeight = window.screen.height;
  
      //  显示高度大于屏幕高度
      // 该页面有引入goodsList组件
      // 离底部不到100 通知getNextPage获取下一页
      if ((scrollHeight > windowHeight) && this.$refs.goodsListComponent.clientHeight !=0 &&  (scrollHeight - scrollTop - windowHeight < 100)) {
      //  发送三次
       if (this.canSendHttp == true) {
             this.canSendHttp = false;
              // console.log("发送请求")
             this.getNextPage();
        }
      }
    },

    // 跳转商品详情页
    goToGoodsDetails(id) {
      // this.$router.push({ name: 'GoodsDetails', params: { id: id }})
      this.$router.push({ path: `/GoodsDetails/${id}` });
    }
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  components: {}
};
</script>

<style scoped>
.
.infinite-list-wrapper {
  height: 100%;
}
.goodsListCard {
  margin: 10px 6px;
}
.otherInfo {
  white-space: nowrap;
}
.goodsTitle {
  color: #303133;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  height: 40px;
  line-height: 19px;
  /* text-align: left; */
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>