<template>
  <div>
    <div class="search" fixed @click="goSearch()">
      <!-- 首页搜索栏 -->
      <mt-search cancel-text="取消" placeholder="搜索" class="theme" fixed></mt-search>
    </div>

    <!-- 首页轮播图 -->
    <div class="swipe">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item,index) in swipes" :key="index">
          <el-image
            style="height=150px"
            :src="item"
          />
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!-- 首页分类栏 -->
    <div class="tab-container">
      <mt-tab-container v-model="selected" :swipeable="true ">
        <mt-tab-container-item id="1">
          <el-row :gutter="30">
            <el-col :span="6" v-for="(item,index) in superCateGory" :id="item.cid" :key="index">
              <div class="grid-content bg-purple" v-if="index < 8">
                <div class="block" @click="getSuperCategoryGoodsList(item.cid,item.cname)">
                  <el-image class="iconImage" :src="item.cpic"></el-image>
                  <span class="demonstration">{{item.cname}}</span>
                </div>
              </div>
            </el-col>
          </el-row>
        </mt-tab-container-item>

        <mt-tab-container-item id="2">
          <el-row :gutter="30">
            <el-col :span="6" v-for="(item,index) in superCateGory" :id="item.cid" :key="index">
              <div class="grid-content bg-purple" v-if="index > 8">
                <div class="block">
                  <el-image class="iconImage" :src="item.cpic"></el-image>
                  <span class="demonstration">{{item.cname}}</span>
                </div>
              </div>
            </el-col>
          </el-row>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <!-- 商品列表栏 -->
    <div class="goodsList-container">

<keep-alive>
      <GoodsList :goodsList="goodsList" @nextPage="getNextPage"></GoodsList>
</keep-alive>
    </div>
  </div>
  
</template>

<script>
import GoodsList from "./subComponents/GoodsList";
export default {
  name:"Home",
  data() {
    return {
      swipes:[
        'https://img.alicdn.com/imgextra/i2/2053469401/O1CN01Dd9Bge2JJhzDdyGZz_!!2053469401.jpg',
        'https://img.alicdn.com/imgextra/i3/2053469401/O1CN01aomudt2JJhzAxhMl1_!!2053469401.jpg',
        'https://img.alicdn.com/imgextra/i1/2053469401/O1CN01yBXj4Q2JJhzAxf5Ma_!!2053469401.jpg'
      ],
      selected: "1",
      superCateGory: [],

      cid: 0,
      subcid: 0,

      pageNum: 1,
      pageSize: 20,
      pages: 0,
      total: 0,
      goodsList: []
    };
  },
  created: function() {
    this.getSuperCateGory();
    this.getSelectGoodsList();
  },
  methods: {
    // 获取超级分类
    getSuperCateGory() {
      this.$http.get("category/selectSuperCateGory.action").then(function(res) {
        // console.log('res:' + JSON.stringify(res))

        var result = JSON.parse(JSON.stringify(res.data.result));

        if (result.code) {
          let data = result.data;
          this.superCateGory = data;
        //  console.log("this.superCateGory:" + this.superCateGory);
        }
      });
    },

    //获取超级分类的数据列表
    getSuperCategoryGoodsList(cid,title){
      this.$router.push({ path: `/SuperCateGory/${cid}/0/${title}` });
    },

    //往搜索页跳转
    goSearch(){
      this.$router.push({name:"Search"})
    },

    //获取数据
    getSelectGoodsList() {
      // 按销量排序
      var data = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        twoHoursSales: -1
      };
      this.$http.post("goods/selectGoodsList.action", data).then(function(res) {
        var result = JSON.parse(JSON.stringify(res.data.result));

        if (result.code == 200) {
          let data = result.data;
          //  console.log("json:" + JSON.stringify(data))
          //  追加数组
          this.goodsList = this.goodsList.concat(data.list);
          //  下一页
          this.pageNum = data.nextPage;
          // 总页数
          this.pages = data.pages;
          // 总量
          this.total = data.total;
          // console.log("this.selectGoodsList:" + this.superCateGory);
        }
      });
    },

    //获取下一页数据
    getNextPage() {
     // console.log("寻找下一页");
        //首页不超过100条数据
        if (this.goodsList.length<100) {

            this.getSelectGoodsList();
        }
     // console.log("寻找成功");
    },
    
  },
  components: { GoodsList },
};
</script>

<style scoped>
.swipe {
  margin-top: 5px;
}
.mint-swipe {
  overflow: hidden;
  position: relative;
  height: 150px;
  width: 100%;
  /* border-radius: 7%; */
  padding: 0px 5px;
}

.is-selected .superCategoryText {
  color: #26a2ff !important ;
}

.tab-container {
  height: 180px;
  position: relative;

}
.goodsList-container {
  position: absolute;
  margin-top: 10px;
}
.block {
  margin: 5px;
}
.iconImage {
  border-radius: 100px;
}
.demonstration {
  color: #606266;
  font-size: 12px;
  line-height: 10px;
}

.mint-tab-item {
  display: inherit;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
}
</style>
