<template>
  <div>

     <mt-header title="注册" :fixex="true">
    </mt-header>

<div class="register">
    <b-container fluid>
      <b-row align-v="center">
        <b-col sm="12" md="6" offset-md="3">
          <b-card-group deck>
            <b-card
              bg-variant="Info"
              header="优惠网"
              header-tag="header"
              footer="@ 2019优惠网"
              footer-tag="footer"
            >
              <b-container fluid>
                <b-row class="root">
                  <b-col sm="3">
                    <label>账号</label>
                  </b-col>
                  <b-col sm="9">
                    <b-form-input type="text" v-model="user.userName"></b-form-input>
                  </b-col>
                </b-row>

                <br />
                <b-row class="password">
                  <b-col sm="3">
                    <label>密码</label>
                  </b-col>
                  <b-col sm="9">
                    <b-form-input type="password" v-model="user.userPassword"></b-form-input>
                  </b-col>
                </b-row>
                <br />

                  <b-row class="password">
                  <b-col sm="3">
                    <label>手机</label>
                  </b-col>
                  <b-col sm="9">
                    <b-form-input type="number" v-model="user.phone"></b-form-input>
                  </b-col>
                </b-row>
                <br />

                <mt-button type="danger" size="large" @click="register()">注册</mt-button>
                  <br />
                <mt-button type="primary" size="large" @click="goLogin()" >登录</mt-button>
              </b-container>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </b-container>


    

</div>


  </div>
</template>

<script>
import { Toast } from "mint-ui";
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
export default {
  data() {
    return {
      user: {
        userName: "",
        userPassword: "",
        phone:""
      }
    };
  },
  methods: {
    register() {
      if (this.user.userName != "" && this.user.userPassword != ""  && this.user.phone.length == 11) {
        //登录
        this.$http.post("user/userRegister.action", this.user).then(function(res) {
          var result = JSON.parse(JSON.stringify(res.body.result));

         // console.log(result);
          if (result.code == -1) {
            Toast({
              message: result.msg,
              // iconClass: 'el-icon-info',
              position: "center",
              duration: 3000
            });
          } else if (result.code == 200) {
          
            Toast({
              message: "注册成功",
              // iconClass: 'el-icon-info',
              position: "center",
              duration: 3000
            });
            this.$router.history.go(-1);
          }
        });
      } else {
        //信息错误
        Toast({
          message: "请正确输入信息",
          // iconClass: 'el-icon-info',
          position: "center",
          duration: 3000
        });
      }
    },
    goLogin(){
       this.$router.push({name:"Login"});
    }
  },
      beforeRouteLeave(to, from, next) {
    // console.log("test：" + (to.path.indexOf("GoodsDetails") != -1))
    to.meta.keepAlive = false
    next();
}, 
  components: {}
};
</script>

<style>
</style>
