import Vue from 'vue'
import App from './App.vue'
import Resource from 'vue-resource'
import store from './store'
import router from './router'
import mui from '@/assets/dist/js/mui.js'
import '@/assets/dist/css/mui.css'

import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)

  

Vue.use(Mint)
Vue.use(ElementUI)


Vue.prototype.$mui = mui

Vue.config.productionTip = false

Vue.use(Resource)


Vue.http.options.xhr = { withCredentials: true }
Vue.http.options.emulateJSON = true

//获取本地访问地址
//vue.config.js代理到服务器地址
 Vue.http.options.root = window.location.href.substring(0,window.location.href.indexOf('#'))



 mui.init({
  //监听Android手机的back、menu按键
  keyEventBind: {
    backbutton: true,  //Boolean(默认true)关闭back按键监听
    menubutton: true   //Boolean(默认true)关闭menu按键监听
  },    
});  

Vue.directive('title', {
  inserted: function (el) {
    document.title = el.dataset.title
  }
})


new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
