import Vue from 'vue'
import Router from 'vue-router'

import Main from '../components/Main'
import Home from '../components/Home'
import CardBag from '../components/CardBag'
import Collection from '../components/Collection'
import Category from '../components/Category'
import Personal from '../components/Personal'

import GoodsDetails from '../components/subComponents/GoodsDetails'

import Login from '../components/subComponents/Login'
import Register from '../components/subComponents/Register'
import SuperCateGory from '../components/subComponents/SuperCateGory'

import Search from '../components/subComponents/Search'
import SearchResult from '../components/subComponents/SearchResult'
import MerchantShop from '../components/subComponents/MerchantShop'

// import GoodsList from '../components/subComponents/GoodsList'

Vue.use(Router)

export default new Router({ 
  mode: 'hash',//history
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      redirect:Home,
      meta: {keepAlive: true},
    },
    {
      path: '/Main',
      name: 'Main',
      component: Main,
      redirect:Home,
      meta: {keepAlive: true},
      children:[
        {
          path: '/Home',
          name: 'Home',
          component: Home,
          meta: {keepAlive: true},
        },
        {
          path: '/CardBag',
          name: 'CardBag',
          component: CardBag,
          meta: {keepAlive: false},
        },
        {
          path: '/Collection',
          name: 'Collection',
          component: Collection,
          meta:{ keepAlive: false},
        },
        {
          path: '/Category',
          name: 'Category',
          component: Category,
          meta: {keepAlive: true},
        },
        {
          path: '/Personal',
          name: 'Personal',
          component: Personal,
          meta:{ keepAlive: false},
        },
        {
          path: '/Register',
          name: 'Register',
          component: Register,
          meta: {keepAlive: false},
        },
      ]
    },
        {
          path: '/GoodsDetails/:id',
          name: 'GoodsDetails',
          component: GoodsDetails,
          meta: {keepAlive: false},
        },
        {
          path: '/SuperCateGory/:cid/:subcid/:title',
          name: 'SuperCateGory',
          component: SuperCateGory,
          meta: {keepAlive: false},
          
        },
        {
          path: '/Search',
          name: 'Search',
          component: Search,
          meta: {keepAlive: true},
          
        },
        {
          path: '/SearchResult/:keyWorlds',
          name: 'SearchResult',
          component: SearchResult,
          meta: {keepAlive: false},
        },
        {
          path: '/MerchantShop/:sellerId/:shopName',
          name: 'MerchantShop',
          component: MerchantShop,
          meta: {keepAlive: false},
        },
        {
          path: '/Login',
          name: 'Login',
          component: Login,
          meta: {keepAlive: false},
        }
    
  ],
  
})