<template>
  <div style="background: white;">
    <div v-if="goodsDetail!=null && swiperImgs!=null">

      <div style="margin-bottom:55px">
      <!-- 轮播图 -->
      <div class="swiper-container" style="height:350px">
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(item,index) in swiperImgs" :key="index">
            <el-image :src="item" :preview-src-list="swiperImgs" fill="fill">
              <div slot="placeholder" class="image-slot">
                加载中
                <span class="dot">...</span>
              </div>
            </el-image>
          </mt-swipe-item>
        </mt-swipe>
      </div>


      <!-- 商品详情信息 -->
      <div class="goodsDetailInfo">
        <el-row>
          <!-- line-1 -->
          <div class="line line-1">
            <!-- 天猫店标签 -->
            <el-col :span="4" v-if="goodsDetail.shopType==1">
              <el-tag type="danger" size="mini" effect="plain">
                <span>天猫店</span>
              </el-tag>
            </el-col>

            <el-col :span="20" style="text-align:left">{{goodsDetail.dtitle}}</el-col>
          </div>

          <!-- line-2 -->
          <div class="line line-2">
            <el-col :span="18" :offset="1" style="text-align:left">
              <span style="color:#f56c6c; font-size:12px">
                券后价
                <span style="font-size:14px">￥</span>
                <span style="font-size:20px">{{goodsDetail.actualPrice}}</span>
              </span>
              <span
                style=" text-decoration: line-through; color:#909399;font-size:14px;"
              >￥{{goodsDetail.originalPrice}}</span>
            </el-col>
            <!-- 活动类型 -->
            <el-col :span="4" style="text-align:right">
              <el-tag type="success" size="mini" effect="plain">
                <span v-if="goodsDetail.activityType==1">领劵活动</span>
                <span v-if="goodsDetail.activityType==2">淘抢购</span>
                <span v-if="goodsDetail.activityType==3">聚划算</span>
              </el-tag>
            </el-col>
          </div>

          <!-- line-3 -->
          <div class="line line-3">
            <el-col :span="8">
              <h6>
                两小时售:
                <span style="color: #f56c6c">{{goodsDetail.twoHoursSales }}</span> 件
              </h6>
            </el-col>
            <el-col :span="8">
              <h6>
                当日售:
                <span style="color: #f56c6c">{{goodsDetail.dailySales }}</span> 件
              </h6>
            </el-col>
            <el-col :span="8">
              <h6>
                当月售:
                <span style="color: #f56c6c">{{goodsDetail.monthSales }}</span> 件
              </h6>
            </el-col>
          </div>

          <!-- line-4 -->
          <div class="line line-4" @click="getPrivilegeLink">
            <el-col :span="24">
              <el-row>
                <el-col :span="14">
                  <div style="font-size:16px;color:white;letter-spacing:3px">
                    ￥
                    <span style="font-size:24px">{{ goodsDetail.couponPrice}}</span>元优惠券
                  </div>

                  <div
                    style="font-size:8px;color: aliceblue;"
                  >使用期限:{{ (goodsDetail.couponStartTime).substring(0,(goodsDetail.couponStartTime ).indexOf(' '))}} --- {{ (goodsDetail.couponEndTime).substring(0,(goodsDetail.couponEndTime).indexOf(' '))}}</div>
                </el-col>
                <el-col :span="10" style="font-size:16px;color:white;margin-top:10px">立即领券</el-col>
              </el-row>
            </el-col>
          </div>
          <!-- line-5 -->
          <div class="line line-5">
            <h5
              style="text-align:left; color:#909399;font-size:12px;line-height: 18px;"
            >{{goodsDetail.desc}}</h5>
          </div>
        </el-row>
      </div>

      <!-- 空白区 -->
      <div class="blackDiv" style="height:5px;width:100%;background:#efeff4"></div>

      <!-- 店家详情信息 -->
      <div class="shopDetailInfo" style="margin: 5px 10px;" @click="goToMerchantShop()">
        <el-row>
          <div class="shopNameOrLogo" style="height:55px">
            <!-- 店家名字 logo -->
            <el-col :span="12">
              <el-row>
                <el-col :span="10">
                  <el-image
                    style="width: 50px; height:50px"
                    src="https://sr.ffquan.cn/relate_pic/o_1dlac36ba195j14m91u5b1jc21464d.jpg"
                    cover="cover"
                  ></el-image>
                </el-col>

                <el-col :span="13" :offset="1" style="text-align:left">
                  <div class="shopName" >{{goodsDetail.shopName}}</div>
                  <div style="margin-top:5px">
                    <el-tag type="danger" size="mini" effect="plain">
                      <span v-if="goodsDetail.shopType ==0 ">淘宝</span>
                      <span v-if="goodsDetail.shopType ==1 ">天猫</span>
                    </el-tag>
                  </div>
                </el-col>
              </el-row>
            </el-col>
            <!-- 店家店铺 -->
            <el-col :span="12">
              <h5
                style="letter-spacing:1px; margin-top: 20px; font-size: 12px;text-align: right;"
              ><el-link type="primary">查看店铺所有优惠 ></el-link></h5>
            </el-col>
          </div>
          <hr style="background: coral;" />

          <div class="shopScore">
            <el-col :span="8">
              <h6>
                宝贝描述:
                <span style="color: #f56c6c">{{goodsDetail.descScore }}</span>
                <i v-if="goodsDetail.descScore>=4.9" class="el-icon-top" style="color:red"></i>
                <i v-if="goodsDetail.descScore<4.9" class="el-icon-bottom" style="color:#67c23a"></i>
              </h6>
            </el-col>
            <el-col :span="8">
              <h6>
                卖家服务:
                <span style="color: #f56c6c">{{goodsDetail.shipScore }}</span>
                <i v-if="goodsDetail.shipScore>=4.9" class="el-icon-top" style="color:red"></i>
                <i v-if="goodsDetail.shipScore<4.9" class="el-icon-bottom" style="color:#67c23a"></i>
              </h6>
            </el-col>
            <el-col :span="8">
              <h6>
                物流服务:
                <span style="color: #f56c6c">{{goodsDetail.serviceScore }}</span>
                <i v-if="goodsDetail.serviceScore>=4.9" class="el-icon-top" style="color:red"></i>
                <i v-if="goodsDetail.serviceScore<4.9" class="el-icon-bottom" style="color:#67c23a"></i>
              </h6>
            </el-col>
          </div>
        </el-row>
      </div>

      <!-- 空白区 -->
      <div class="blackDiv" style="height:5px;width:100%;background :#efeff4"></div>

      <el-button  v-if="showGoodsDetailsImgs == false" @click="showGoodsDetailsImgs = true">点击查看详情图</el-button>


      <!-- 商品详情图 -->
      <div v-if="showGoodsDetailsImgs" class="goodsDetailsImgs">
        <el-row>
          <el-col :span="24">
            <el-image
              v-for="(detailImgItem,index) in detailPics"
              :key="index"
              :src="detailImgItem"
              lazy
            >
            </el-image>
          </el-col>
        </el-row>
      </div>

      </div>

      <!-- 底部工具 -->
      <mt-tabbar :fixed="true">
        <mt-tab-item id="分享">
          <img slot="icon" src="../../assets/icon/分享0.png" @click="getPrivilegeLink" />
          <div @click="getPrivilegeLink">分享</div>
        </mt-tab-item>
        <mt-tab-item id="收藏">
          <slot v-if="isColletion == false">
            <img slot="icon" src="../../assets/icon/collection.png" @click="Colletion()" />
            <div @click="Colletion()">未收藏</div>
          </slot>
          <slot v-if="isColletion == true">
            <img slot="icon" src="../../assets/icon/collection_click.png" @click="Colletion()" />
            <div @click="Colletion()">已收藏</div>
          </slot>
        </mt-tab-item>
        <mt-tab-item id="领券购买">
          <!-- <img slot="icon" src="../../assets/icon/立即领取.png">
          立即领取-->
          <div class="lingqu" @click="getPrivilegeLink">
            <h5 style="padding-top: 10px;color: white;">领券购买</h5>
          </div>
        </mt-tab-item>
      </mt-tabbar>

      <!-- 模态框领劵 -->
      <el-dialog title="淘口令" :visible.sync="visible" class="my-dialog" style="margin-top: -8vh">
        <div>
          <h4>{{goodsDetail.dtitle}}</h4>
        </div>
        <div>
          <el-image style="width: 250px; height: 250px" :src="goodsDetail.mainPic" fit="fit"></el-image>
        </div>
        <br />
        <div>
          【原价】
          <span class="dialogFont">{{goodsDetail.originalPrice}}</span>元
        </div>
        <div>
          【现价】
          <span class="dialogFont">{{goodsDetail.actualPrice}}</span>元
        </div>
        <br />
        <div>【复制此信息打开手机淘宝即可查看下单】</div>
        <div>
          <span class="dialogFont">{{this.tpwd}}</span>
        </div>
        <div>
          【链接】
          <a :href="this.shortUrl">{{this.shortUrl}}</a>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button
            class="tag-read"
            type="danger"
            @click="cloneTpwd"
            :data-clipboard-text=" '【' + goodsDetail.dtitle  + '】' + '\n【淘口令】 ' + this.tpwd + '\n【链接】' + this.shortUrl "
          >复制到淘宝打开</el-button>
          <el-button @click="visible = false">取 消</el-button>
        </div>
      </el-dialog>
    </div>
     <div v-if="goodsDetail==null || swiperImgs == null">
       <img src="../../assets/imgs/已售馨.png" style="height:100%;width:100%"/>
     </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import Clipboard from "clipboard";
export default {
  name:"GoodsDetails",
  data() {
    return {
      id: this.$route.params.id,
      // id: 22993562,
      goodsDetail: null,
      swiperImgs: [],
      detailPics: [],

      shortUrl: null,
      tpwd: null,
      visible: false,
      user: this.$store.getters.getUser,
      isColletion: false,
      showGoodsDetailsImgs: false
    };
  },
  created: function() {
    //console.log("获取单品");
    this.getGoodsDetailById();
    this.GoodsIsColletion();
  },
  methods: {
    //收藏
    Colletion() {
      //用戶不为空
      if (this.user != null) {
        //执行收藏
        var collction = {
          userId: this.user.userId,
          goodsId: this.id
        };
        //console.log("collction:" + collction);
        if (this.isColletion == false) {
          this.$http
            .post("goodsCollection/addCollection.action", collction)
            .then(function(res) {
              var result = JSON.parse(JSON.stringify(res.body.result));
              //收藏成功
              if (result.code == 200) {
                Toast({
                  message: "收藏成功",
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 3000
                });
                //设置收藏成功
                this.isColletion = true;
              } else {
                //收藏失败
                Toast({
                  message: result.msg,
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 3000
                });
              }
            });
        } else if (this.isColletion == true) {
          //执行取消收藏
          this.$http
            .post("goodsCollection/deleteCollection.action", collction)
            .then(function(res) {
              var result = JSON.parse(JSON.stringify(res.body.result));
              //取消收藏成功
              if (result.code == 200) {
                Toast({
                  message: "取消收藏成功",
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 3000
                });
                //设置取消收藏成功成功
                this.isColletion = false;
              } else {
                //取消收藏成功失败
                Toast({
                  message: result.msg,
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 3000
                });
              }
            });
        }
      } else {
        Toast({
          message: "暂未登陆,请前往登陆",
          // iconClass: 'el-icon-info',
          position: "center",
          duration: 3000
        });
      }
    },

    //添加卡包
    addCardBag(){
      //用戶不为空
      if (this.user != null) {
        //执行收藏
        var collction = {
          userId: this.user.userId,
          goodsId: this.id
        };
        //console.log("collction:" + collction);
          this.$http
            .post("goodsCardBag/addCardBag.action", collction)
            .then(function(res) {
              var result = JSON.parse(JSON.stringify(res.body.result));
              console.log(result)
              //收藏成功
              if (result.code == 200) {
                // Toast({
                //   message: "收藏成功",
                //   // iconClass: 'el-icon-info',
                //   position: "center",
                //   duration: 3000
                // });
                //设置收藏成功
                // this.isColletion = true;
              } else {
                //收藏失败
                // Toast({
                //   message: result.msg,
                //   // iconClass: 'el-icon-info',
                //   position: "center",
                //   duration: 3000
                // });
              }
            });
      } else {
        Toast({
          message: "暂未登陆,请前往登陆",
          // iconClass: 'el-icon-info',
          position: "center",
          duration: 3000
        });
      }
    },

    //查看是否已收藏
    GoodsIsColletion() {
      if (this.user != null) {
        var collction = {
          userId: this.user.userId,
          goodsId: this.id
        };

        this.$http
          .post("goodsCollection/isCollection.action", collction)
          .then(function(res) {
            var result = JSON.parse(JSON.stringify(res.body.result));
            //設置已收藏
            if (result.code == 200) {
              this.isColletion = true;
            } else {
              //設置为收藏
              this.isColletion = false;
            }
          });
      } else {
        this.isColletion = false;
      }
    },

    // 获取商品单品信息
    getGoodsDetailById() {
      this.$http
        .get("goods/getGoodsDetailById.action?id=" + this.id)
        .then(function(res) {
          var result = JSON.parse(JSON.stringify(res.body.result));

          if (result.code) {
            this.goodsDetail = result.data;

            // 解析轮播图
            var imgsTemp = this.goodsDetail.imgs;
            this.swiperImgs = imgsTemp.split(",");

            // 解析商品详情图
            var detailPicsTemp = this.goodsDetail.detailPics;
            this.detailPics = detailPicsTemp.split(",");
            // console.log("this.goodsDetail:" + JSON.stringify(this.goodsDetail));
          }
        });
    },

    // 获取商品淘口令 短连接高佣接口
    getPrivilegeLink() {
      if (this.tpwd != null && this.shortUrl != null) {
        this.visible = true;
      } else {
        this.$http
          .get(
            "privilegeLink/getPrivilegeLink.action?goodsId=" +
              this.goodsDetail.goodsId
          )
          .then(function(res) {
            var result = JSON.parse(JSON.stringify(res.body.result));
            Toast({
              message: "正在领取",
              // iconClass: 'el-icon-info',
              position: "bottom",
              duration: 1000
            });
            if (result.code) {
             // console.log("result:" + JSON.stringify(result.data));
              this.shortUrl = result.data.shortUrl;
              this.tpwd = result.data.tpwd;
              this.visible = true;

              //添加卡包
              this.addCardBag();

              Toast({
                message: "领取成功",
                // iconClass: 'el-icon-info',
                position: "bottom",
                duration: 1000
              });
            } else {
              Toast({
                message: "领取失败",
                // iconClass: 'el-icon-info',
                position: "bottom",
                duration: 1000
              });
            }
          });
      }
    },

    // 复制短连接 淘口令信息
    cloneTpwd() {
      var clipboard = new Clipboard(".tag-read");
      clipboard.on("success", e => {
        this.visible = false;
        Toast({
          message: "复制成功",
          // iconClass: 'el-icon-info',
          position: "center",
          duration: 2000
        });
       // console.log("复制成功"); // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        // 不支持复制
      //  console.log("该浏览器不支持自动复制"); // 释放内存
        clipboard.destroy();
      });
    },

    //前往商家店铺
    goToMerchantShop(){
         this.$router.push({ path: `/MerchantShop/${this.goodsDetail.sellerId}/${this.goodsDetail.shopName}` });
    }


  },
  beforeRouteLeave(to, from, next) {
    // console.log("test：" + (to.path.indexOf("GoodsDetails") != -1))
    if (to.path.indexOf("MerchantShop") != -1) {
        from.meta.keepAlive = true;
         to.meta.keepAlive = false;
    }else{
          from.meta.keepAlive = false;
          to.meta.keepAlive = true;
    }
    next();
},
  components: {}
};
</script>

<style scoped>
.goodsDetailInfo {
  padding: 7px;
}
.line {
  padding: 15px 0px;
}
.line-4 {
  margin-top: 20px;
  border-radius: 6%;
  height: 70px;
  background: linear-gradient(to right, #fda75b, #f950b7);
}
.lingqu {
  border-radius: 15%;
  height: 35px;
  background: linear-gradient(to right, #fda75b, #f950b7);
}

.my-dialog >>> .el-dialog {
  width: 90%;
}

.dialogFont {
  color: red;
  font-size: 18px;
}
.shopName{
  font-size: 12px;verflow: hidden;/*超出部分隐藏*/
    text-overflow:ellipsis;/* 超出部分显示省略号 */
    white-space: nowrap;/*规定段落中的文本不进行换行 */
    width: 96px;/*需要配合宽度来使用*/
}
</style>

