import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {

      user: JSON.parse(localStorage.getItem("user")) ||{
          userId: 0,
          userName: '',
          userPassword: '',
          phone: ''
      },
      isAndroid: true //修改此项 切换版本  为false时：Html  为true：Android

    },
    //获取
    //store.getters.getUser
    getters: {

      getUser: state=>{



          if(state.user.userId == 0){
              return null
          }

          if(state.user.userName == '' || state.user.userName ==null){
              return null;
          }
          if(state.user.userPassword == '' || state.user.userPassword ==null){
            return null;
        }
          return state.user
      },
      
      getIsAndroid: state=>{
        return state.isAndroid
      }

    },
    //设置
    //store.commit('setUser', user）
    mutations:{
        setUser(state,user){
            localStorage.setItem('user', JSON.stringify(user));
            state.user = user
        },
        setIsAndroid(state,flag){
          state.isAndroid = flag
      }
    }
  })