<script>
export default {
  data() {
    return {
      user: this.$store.getters.getUser
    };
  },
  created: function() {
    this.openPersonal();
  },
  methods: {
    openPersonal() {
      //当打开页面vuex中没有user时候 执行登录页面造作 跳转到登录页面
     // console.log(this.$store.getters.getUser);
      if (
        this.$store.getters.getUser == null ||
        this.$store.getters.getUser == ""
      ) {
           console.log("error");
        this.$router.push({ name: "Login" });
      }else{
          console.log("success");
      }
    },
    myColletion(){
      this.$router.push({ name: "Collection" });
    },

    myCardBag(){
     this.$router.push({ name: "CardBag" });
    },
    logout(){
      console.log("退出")
      localStorage.removeItem('user')

      if (this.$store.getters.getIsAndroid) {
        //提示安卓退出登出
        try{
         window.android.callAndroidLogoutSuccess();
          console.log("正在结束运行安卓端")
        }catch(err){
      console.log("正在结束运行网页端")
       this.$store.commit('setIsAndroid', true)
        }
      }

      this.$router.push({name:"Login"})
    }
  },
  components: {}
};
</script>

