<template>
  <div>
    <div class="search">
      <el-row>
        <el-col :span="20">
          <!-- 首页搜索栏 -->
          <mt-search
            cancel-text="取消"
            v-model="keyWords"
            placeholder="搜索框"
            class="theme"
            fixed="true"
          >
          </mt-search>
        </el-col>
        <!-- 搜索按钮 -->
        <el-col :span="4">
          <el-button type="primary" icon="el-icon-search" style="height:44px;" @click="getSearchByKeyWorld(keyWords)"></el-button>
        </el-col>
      </el-row>

      <!-- 搜索结果 -->
      <div class="searchResult">
         <mt-cell v-if="SearchSuggestions.length==0 && (keyWords!=null && keyWords!='')" :title="keyWords + ' 暂无联想词'"></mt-cell>
        <mt-cell
          v-for="(item, index) in SearchSuggestions"
          :key="index"
          :title="item.kw"
          :value="item.total"
          @click.native="getSearchByKeyWorld(item.kw)"
        >
        </mt-cell>
      </div>
    </div>

    <!-- 热搜显示 -->
    <h3 style="color:#606266;">热搜</h3>
    <div class="hotSearch">
      <el-tag
        v-for="(tag, index) in hotSearchs"
        :key="index"
        effect="plain"
        :type="tagType[Math.floor(Math.random() * tagType.length)]"
        @click="getSearchByKeyWorld(tag)"
      >
        {{ tag }}
      </el-tag>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      keyWords: null,
      hotSearchs: [],
      SearchSuggestions: [],
      tagType: ["", "success", "info", "warning", "danger"],
      
    };
  },
  watch: {
    //监控查询
    keyWords: function(newVal, oldVal) {
      if (newVal != null && newVal != oldVal) {
        //从后台获取关键词
        this.getSearchSuggestion();
      }
    }
  },
  created: function() {
    this.getHotSearch();
  },
  methods: {
    //获取热搜
    getHotSearch() {
      this.$http.get("search/getHotSearch.action").then(function(res) {
        var result = JSON.parse(JSON.stringify(res.data.result));
        //console.log(result);
        if (result.code == 200) {
          this.hotSearchs = result.data;
          this.hotSearchs = this.hotSearchs.slice(0, 40);
        }
      });
    },

    //获取联想词
    getSearchSuggestion() {
      this.$http
        .get("search/getSearchSuggestion.action?keyWords=" + this.keyWords)
        .then(function(res) {
          var result = JSON.parse(JSON.stringify(res.data.result));
         // console.log(result);
          if (result.code == 200) {
            this.SearchSuggestions = result.data;
          }
        });
    },

    //通过关键词搜索获取结果
    getSearchByKeyWorld(keyWords){
      keyWords = encodeURI(keyWords)
     // console.log("keyWords:" + keyWords)
      this.$router.push({ path: `/SearchResult/${keyWords}` })
    },

  },
  components: {}
};
</script>

<style scope>
.el-tag + .el-tag {
  margin-left: 10px;
  margin-top: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.searchResult{
      z-index: 999;
    position: fixed;
    width: 100%;
}
</style>
