<template>
  <div>
    <div class="searchAndTool">
      <!-- 首页搜索栏 -->
      <div class="search"  @click="goSearch()">
      <mt-search
        cancel-text="取消"
        :placeholder="keyWorlds"
        class="theme"
      ></mt-search>
      </div>


        <div class="GoodsListTool">
          <mt-navbar v-model="selected">
      <mt-tab-item id="1" is-selected>人气↓</mt-tab-item>
      <mt-tab-item id="2">销量↓</mt-tab-item>
      <mt-tab-item id="3">券后价↑</mt-tab-item>
      <mt-tab-item id="4">券后价↓</mt-tab-item>
    </mt-navbar>
        </div>
    </div>


<div class="goodsList">
    <GoodsList :goodsList="goodsList" @nextPage="getNextPage"></GoodsList>
</div>
  </div>
</template>

<script>
import GoodsList from "./GoodsList";
export default {
  data() {
    return {
      selected: 2,
      pageNum: 1,
      pageSize: 20,
      pages: 0,
      total: 0,
      goodsList: [],
      keyWorlds: decodeURI(this.$route.params.keyWorlds)
    };
  },

  methods: {
    //获取搜索结果
    getSearchResult() {
      //当搜索词与以往不同时 重新初始化

      var data = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        monthSales: -1,
        title: this.keyWorlds
      };

      this.$http.post("goods/selectGoodsList.action", data).then(function(res) {
        var result = JSON.parse(JSON.stringify(res.data.result));

        if (result.code == 200) {
          let data = result.data;
          //  console.log("json:" + JSON.stringify(data))
          //  追加数组
          this.goodsList = this.goodsList.concat(data.list);
          //  下一页
          this.pageNum = data.nextPage;
          // 总页数
          this.pages = data.pages;
          // 总量
          this.total = data.total;
        }
      });
    },

    //获取下一页数据
    getNextPage() {

      this.getSearchResult();

    },

    //跳转搜索页
    goSearch() {
      this.$router.push({ name: "Search" });
    },

    //排序方式
    orderGoodsList() {
      if (this.selected == 1) {
        //按照 couponReceiveNum 人气降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.couponReceiveNum < item2.couponReceiveNum) return 1;
          if (item1.couponReceiveNum > item2.couponReceiveNum) return -1;
          return 0;
        });
      }
      if (this.selected == 2) {
        //按照 monthSales 销量降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.monthSales < item2.monthSales) return 1;
          if (item1.monthSales > item2.monthSales) return -1;
          return 0;
        });
      }

      if (this.selected == 3) {
        //按照 actualPrice 销量升序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.actualPrice < item2.actualPrice) return -1;
          if (item1.actualPrice > item2.actualPrice) return 1;
          return 0;
        });
      }

      if (this.selected == 4) {
        //按照 actualPrice 销量降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.actualPrice < item2.actualPrice) return 1;
          if (item1.actualPrice > item2.actualPrice) return -1;
          return 0;
        });
      }
    }

  },
beforeRouteLeave(to, from, next) {
    // console.log("test：" + (to.path.indexOf("GoodsDetails") != -1))
    if (to.path.indexOf("GoodsDetails") != -1) {
        from.meta.keepAlive = true;
         to.meta.keepAlive = false;
    }else{
        from.meta.keepAlive = false;
          to.meta.keepAlive = false;
    }
    next();
},

    watch: {
    selected: function(newVal, oldVal) {
      if (newVal != oldVal) {
        this.orderGoodsList();
      }
    }
  },
  components: {
    GoodsList
  }
};
</script>

<style scoped>
.searchAndTool{
      top: 1;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 1;
}
.goodsList{
  padding-top: 44px;
}
</style>
