<template>
  <div class="SuperCateGory">
    <!-- 头部 -->
    <mt-header :title="title" :fixed="true"> </mt-header>
    <mt-navbar v-model="selected" class="GoodsListTool">
      <mt-tab-item id="1" is-selected>人气↓</mt-tab-item>
      <mt-tab-item id="2">销量↓</mt-tab-item>
      <mt-tab-item id="3">券后价↑</mt-tab-item>
      <mt-tab-item id="4">券后价↓</mt-tab-item>
    </mt-navbar>

    <div class="goodsListBox">
      <keep-alive>
        <GoodsList :goodsList="goodsList" @nextPage="getNextPage"></GoodsList>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import GoodsList from "./GoodsList";
export default {
  data() {
    return {
      selected: 2,
      cid: this.$route.params.cid,
      subcid: this.$route.params.subcid,
      title: this.$route.params.title,
      pageNum: 1,
      pageSize: 20,
      pages: 0,
      total: 0,
      goodsList: [],
      flag: true
    };
  },
  created: function() {
    this.getGoodsListByCategory();
  },
  methods: {
    //通过分类获取数据
    getGoodsListByCategory() {
      //一级分类会默认屏蔽二级分类
      //如果二级分类存在 则使用二级分类
      //如果还有下一页
      if (this.flag) {
      var data = {};
      if (this.subcid != 0) {
        //使用二级分类
        data = {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          subcid: this.subcid
        };
      } else {
        //使用一级分类
        data = {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          cid: this.cid
        };
      }

      //发送请求
      this.$http.post("goods/selectGoodsList.action", data).then(function(res) {
        var result = JSON.parse(JSON.stringify(res.data.result));
        //console.log(result);
        if (result.code == 200) {

          //判断数组是否有包含关系
          result.data.list.forEach(item => {
            var id = item.id;
            this.goodsList.forEach(goodsListItem => {
              if (goodsListItem.id == id) {
                this.flag = false;
                return false;
              }
            });
          });

          //没包含关系加入goodslist
          if (this.flag == true) {
            //  追加数组
            this.goodsList = this.goodsList.concat(result.data.list);
            //  下一页
            this.pageNum = result.data.nextPage;
            // 总页数
            this.pages = result.data.pages;
            // 总量
            this.total = result.data.total;
          }
        }
      });
      }
    },
    //获取下一页
    getNextPage() {
      this.getGoodsListByCategory();
    },
    //排序方式
    orderGoodsList() {
      if (this.selected == 1) {
        //按照 couponReceiveNum 人气降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.couponReceiveNum < item2.couponReceiveNum) return 1;
          if (item1.couponReceiveNum > item2.couponReceiveNum) return -1;
          return 0;
        });
      }
      if (this.selected == 2) {
        //按照 monthSales 销量降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.monthSales < item2.monthSales) return 1;
          if (item1.monthSales > item2.monthSales) return -1;
          return 0;
        });
      }

      if (this.selected == 3) {
        //按照 actualPrice 销量升序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.actualPrice < item2.actualPrice) return -1;
          if (item1.actualPrice > item2.actualPrice) return 1;
          return 0;
        });
      }

      if (this.selected == 4) {
        //按照 actualPrice 销量降序排序
        this.goodsList.sort(function(item1, item2) {
          if (item1.actualPrice < item2.actualPrice) return 1;
          if (item1.actualPrice > item2.actualPrice) return -1;
          return 0;
        });
      }
    }
  },
  watch: {
    selected: function(newVal, oldVal) {
      if (newVal != oldVal) {
        this.orderGoodsList();
      }
    }
  },
  components: {
    GoodsList
  }
};
</script>

<style scoped>
.SuperCateGory {
  margin-top: 44px;
}
.goodsListBox {
  padding-top: 40px;
}
.GoodsListTool {
  top: 1;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 1;
}

.SuperCateGory >>> .mint-header {
  height: 44px;
}
</style>
