<template>
  <div id="app" v-title data-title="优惠网">
<keep-alive >
		<!-- 当前进入的路由 meta里面 keepAlive为true时走这里 -->
      <router-view v-if="$route.meta.keepAlive == true"></router-view>
    </keep-alive>
    
    <!-- 当前进入的路由 meta里面 keepAlive为false时走这里 下面 if 判断进行了取反处理 -->
    <router-view v-if="$route.meta.keepAlive == false"></router-view>

  </div>
</template>

<script>


export default {
  name: 'app',
  created:function(){
    this.checkUser()
  },
  methods:{
    //如果没有User的localStroage 则跳转登录
    checkUser(){
      if (this.$store.getters.getUser ==null ) {
       this.$router.push({ name: "Login" });
      }
    }
  }
}
</script>

