<template>
  <div>
    <!-- 头部 -->
    <div class="CateGoryHeader">
      <mt-header title="超级分类"> </mt-header>

      <div class="search" @click="goSearch()">
        <!-- 首页搜索栏 -->
        <mt-search
          cancel-text="取消"
          placeholder="搜索"
          class="theme"
        ></mt-search>
      </div>
    </div>
    <div class="superCategory">
      <el-row>
        <!-- 一级分类 左边栏 -->
        <el-col :span="6" class="superCategoryItem">
          <el-button
            class="superCategoryButton"
            v-for="(superItem, superIndex) in superCateGory"
            :id="superItem.cid"
            :key="superIndex"
            @click="activeId = superItem.cid"
            >{{ superItem.cname }}</el-button
          >
        </el-col>
        <!-- 二级分类 右边栏 -->
        <el-col :span="18">
          <mt-tab-container v-model="activeId" :swipeable="true">
            <mt-tab-container-item
              :id="superItem.cid"
              v-for="(superItem, superIndex) in superCateGory"
              :key="superIndex"
            >
              <el-row>
                <el-col
                  :span="6"
                  v-for="(item, index) in superItem.subcategories"
                  :key="index"
                  class="subCategory"
                >
                <div class="subCategotyItem"  @click="getSuperCategoryGoodsList(item.subcid,item.subcname)">
                  <el-avatar size="large"
                    style="width: 80px; height: 80px"
                    :src="item.scpic"
                    fit="fit"
                    class="subCategoryImg"
                  ></el-avatar>
                  <div class="subCategoryTitle">
                    <h6>{{ item.subcname }}</h6>
                  </div>
                  </div>
                </el-col>
              </el-row>
            </mt-tab-container-item>
          </mt-tab-container>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      superCateGory: [],
      activeId: 2
    };
  },
  created: function() {
    this.getSuperCateGory();
  },
  methods: {
    // 获取超级分类
    getSuperCateGory() {
      this.$http.get("category/selectSuperCateGory.action").then(function(res) {
        // console.log('res:' + JSON.stringify(res))

        var result = JSON.parse(JSON.stringify(res.data.result));

        if (result.code) {
          let data = result.data;
          this.superCateGory = data;
          //console.log("this.superCateGory:" + this.superCateGory);
        }
      });
    },

    //获取超级分类的数据列表
    getSuperCategoryGoodsList(subcid, title) {
      this.$router.push({ path: `/SuperCateGory/0/${subcid}/${title}` });
    },

    //往搜索页跳转
    goSearch() {
      this.$router.push({ name: "Search" });
    }
  },
    beforeRouteLeave(to, from, next) {
    // console.log("test：" + (to.path.indexOf("GoodsDetails") != -1))
    if (to.path.indexOf("SuperCateGory") != -1) {
        from.meta.keepAlive = true;
         to.meta.keepAlive = false;
    }else{
          from.meta.keepAlive = true;
          to.meta.keepAlive = true;
    }
    next();
},  
  components: {}
};
</script>

<style scoped>

.CateGoryHeader{
  top: 1;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 1;
}
.superCategory{
  position:relative;
  top: 85px;
 
}
.superCategoryItem {
  background-color: white;
}
.subCategory {
  margin: 10px;
}
.superCategoryItem >>> .el-button + .el-button {
  margin-left: 0px;
}
.subCategoryImg {
  border-radius: 50px;
}
.subCategoryTitle {
  text-align: center;
  width: 80px;
}
</style>
